import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    role: null
  },
  mutations: {
    setRole (state, role) {
      state.role = role
    }
  },
  getters: {
    allowedRouteNames: state => {
      const common = ['logout', 'home']
      switch (state.role) {
        case 'admin':return [
          ...common,
          'cars', 'administration'
        ]
        case 'externalUser':return [
          ...common,
          'home'
        ]
        case 'manager':return [
          ...common,
          'cars'
        ]
        case 'serviceTechnician':return [
          ...common
        ]
        default:return [
          'login'
        ]
      }
    }
  }
})
