<template>
  <v-app>
    <v-toolbar app>
      <v-toolbar-title class="headline text-uppercase">
        <span class="accent--text">KST</span>
        <span class="primary--text font-weight-light">INPRO</span>
      </v-toolbar-title>

      <v-spacer/>

      <v-btn class="hidden-sm-and-down" v-for="({text, to}, i) in items" v-text="text" :key="i" :to="to" flat color="primary"/>
      <v-menu class="hidden-md-and-up" bottom left>
        <v-btn slot="activator" dark icon>
          <v-icon color="accent">mdi-dots-vertical</v-icon>
        </v-btn>

        <v-list>
          <v-list-tile v-for="({text, to}, i) in items" :key="i">
            <v-list-tile-content>
              <v-btn v-text="text" :to="to" block flat color="primary" style="white-space: nowrap;"/>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-menu>
    </v-toolbar>

    <v-content>
      <transition name="fade" mode="out-in">
        <router-view/>
      </transition>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    rawItems: [
      { text: 'home', to: { name: 'home' } },
      { text: 'cars', to: { name: 'cars' } },
      { text: 'administration', to: { name: 'administration' } },
      { text: 'login', to: { name: 'login' } },
      { text: 'logout', to: { name: 'logout' } }
    ]
  }),
  computed: {
    allowedRouteNames () {
      return this.$store.getters.allowedRouteNames
    },
    items () {
      return this.rawItems.filter(item =>
        this.allowedRouteNames.includes(item.to.name)
      ).map(item => ({
        ...item,
        text: this.$t(item.text)
      }))
    }
  }
}
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.3s;
  transition-property: opacity;
  transition-timing-function: ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0
}
</style>
